<template>
  <div class="ds-dormitory-left">
    <div class="title">{{title}}</div>
  </div>
</template>

<script>
  import DormitoryConfig, {leftTree} from "_platform/public/dormitory_config/index"
  export default {
    name: "leftTree",
    data(){
      return{
        title: ""
      }
    },
    created() {
      this.leftTreeConfig = new DormitoryConfig(leftTree);
      Object.assign(this, this.leftTreeConfig);   //赋值部分配置项
    }
  }
</script>