<template>
  <div class="top-tool">
    <div class="title">{{title}}</div>
    <ul class="state-list">
      <li v-for="(item, it) in stateList" :key="item.val">
        <i :style="getSty(item)"></i>
        <span>{{item.txt}}</span>
      </li>
    </ul>
    <el-button @click="goClick">查看</el-button>
  </div>
</template>

<script>
  import DormitoryConfig, {rightLayout} from "_platform/public/dormitory_config";
  export default {
    name: "topTool",
    data(){
      return{
        title: "",
        stateList: []
      }
    },
    created() {
      this.config = new DormitoryConfig(rightLayout);
      Object.assign(this, this.config);
    },
    methods:{
      getSty(item){
        return{
          backgroundColor: item.color,
        }
      },
      goClick(){
        console.log("这里先走了")
        let time  = new Date().toLocaleDateString();
        console.log(time)
      }
    }
  }
</script>